.App {
  text-align: center;
  color: white;
}

.nav-fixed-container
{
  position: fixed;
  width: 100%;
  z-index: 3;
  top: 0;
}

.App-nav-respo-container-off
{
  display: none;
}
.App-nav-container {
  height: 60px;
  background-color: #0E97FF;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.App-nav-logo-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.App-nav-logo {
  height: 45px;
  background: none;
  padding: 0px 5px;
}

.App-nav-bar {
  /*height: 100%;*/
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.App-nav-bar-link-container {
  margin: 0px 30px;
  background: unset;
  border: unset;
  padding-bottom: 13px;
}

.App-nav-bar-link-container:hover {
  margin: 0px 30px;
  padding-bottom: 9px;
  border-bottom: solid 4px #92D1F0;
  text-decoration: none;
}

.App-nav-bar-link-container-active {
  margin: 0px 30px;
  background: unset;
  padding-bottom: 19px;
  border: none;
  border-bottom: solid 4px #fff;
}

.App-nav-bar-link {
  color: #fff;
  text-decoration: unset;
  font-weight: 700;
}

.App-nav-get-started {
  height: 100%;
  background-color: #0E97FF;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  width: 130px;
  color: white;
  font-size: 17px;
  border-color: white;
}

.menu-button
{
  display: none;
}

.App-menu-respo-off
{
  display: none;
}

@media screen and (max-width: 1000px) {
  .App-nav-bar-container {
    display: none;
  }

  .App-nav-container {
    height: 60px;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 3px 6px rgb(11 33 74 / 9%), 0 -2px 2px rgb(11 33 74 / 3%);
    position: fixed;
    width: 100%;
    z-index: 3;
  }

  .App-nav-logo-container {
    display: flex;
    flex-direction: row;
    /*justify-content: space-around;*/
    align-items: center;
    background-color: #0E97FF;
    width: 300px;
    height: 100%;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    box-sizing: border-box;
    padding-right: 60px;
  }

  .App-nav-logo {
    height: 85%;
    padding: 0 5px;
  }

  .App-nav-logo-container .menu-button {
    display: block;
    background-color: #0E97FF;
    color: white;
    font-size: 18px;
    height: 100%;
    line-height: 60px;
    width: fit-content;
    padding: 0 13px;
    text-align: center;
  }

  .App-nav-logo-container p:hover {
    cursor: pointer;
    background-color: white;
    color: #2c2959;
  }

  .App-nav-get-started:hover {
    cursor: pointer;
    color: #eeeaea;
  }

  .App-menu-respo-off {
    display: none;
  }

  .App-menu-respo-on {
    display: block;
    height: fit-content;
  }

  .App-menu-respo-on .App-nav-bar {
    background-color: #127ed3;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    width: 100%;
    height: 350px;
    margin-top: 60px;
  }

  .App-nav-bar-link-container,
